export function getGradeInfo(exp) {
	if(exp<0) return 'err';
	if(exp<100){
		return grade[0];  
	}
	else if(exp<150){
		return grade[1];  
	}
	else if(exp<400){
		return grade[2];  
	}
	else if(exp<900){
		return grade[3];  
	}
	else if(exp<1800){
		return grade[4];  
	}
	else if(exp<3600){
		return grade[5];  
	}
	else if(exp<8000){
		return grade[6];  
	}
	else if(exp<16000){
		return grade[7];  
	}
	else return grade[7];  
}
